<template>
<div class="pt-5">
    <!--loader-->
    <loading :active.sync="isLoading" >
        <div class="dot-loader">
            <span class="dot dot1 bg-use"></span>
            <span class="dot dot2 bg-abstract"></span>
            <span class="dot dot3 bg-warning"></span>
            <span class="dot dot4 bg-alert"></span>
        </div>
        <i class="fas fa-fish text-primary me-2 fa-3x"></i>
        <div class="dot-loader">
            <span class="dot dot5 bg-use"></span>
            <span class="dot dot6 bg-abstract"></span>
            <span class="dot dot7 bg-warning"></span>
            <span class="dot dot8 bg-alert"></span>
        </div>
    </loading> 
    <h2 class="mt-4 manage-header"><i class="fas fa-shopping-cart ms-2"></i></h2>
    <div class="text-end py-3">
        <button class="btn btn-info text-white" data-toggle="modal" @click="openModal(true)">
        <i class="fas fa-plus ms-2"></i></button>
    </div>
    <div class="table-responsive">
        <table class="table table-striped table-sm mt-4 text-third">
            <thead class="border-bottom-2">
                <th width="120"></th>
                <th></th>
                <th width="60"></th>
                <th width="60"></th>
                <th width="120"></th>
                <th width="120"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in products" :key="item.id" v-cloak>
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency}}</td>
                    <td class="text-right">{{item.price | currency}}</td>    
                    <td><span v-if="item.is_enabled"></span><span v-else></span></td>
                    <td><button class="btn btn-outline-third btn-sm" @click="openModal(false,item)"></button>
                        <button class="btn btn-outline-alert btn-sm" @click.prevent="openModalDelete(item)"></button>
                    </td>                    
                </tr> 
            </tbody>
        </table>
        <div v-if="products.length===0&&!isLoading">
            <h3 class="text-center"></h3>
        </div> 
    </div>
    <!------>
    <pagination :pagination="pagination" @getData="getProducts"></pagination>
    <!---Modal--->
    <div class="modal fade"  id="delProductModal" tabindex="-1"  aria-hidden="true">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header bg-danger bg-gradient">
                    <h5 class="modal-title text-white" ><i class="fas fa-trash-alt ms-2"></i></h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                     <strong class="text-danger">{{ currentProduct.title }}</strong>?<br> ()
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-third" data-bs-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger"  @click="deleteProduct"></button>
                </div>
            </div>
        </div>
    </div>
    <!--Modal-->
    <ValidationObserver v-slot="{ invalid }">
        <div class="modal fade"  id="AddNewModal" tabindex="-1"  aria-hidden="true">
            <div class="modal-dialog modal-dialog-scrollable modal-xl">
                <div class="modal-content">
                    <div class="modal-header bg-abstract text-white bg-gradient">
                        <h5 class="modal-title" >{{modaltitle}}<i class="ms-2 fas fa-pencil-alt" ></i></h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <form class="row g-3">
                            <div class="col-12">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="title"><span class="text-danger">*</span></label>
                                    <input type="text" name="" id="title" v-model="currentProduct.title" placeholder="" class="form-control" :class="classes">
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-md-6">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="category"><span class="text-danger">*</span></label>
                                    <select name="" id="category" v-model="currentProduct.category" class="form-select" :class="classes">
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                        <option value=""></option>
                                    </select>
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-md-6">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="unit"><span class="text-danger">*</span></label>
                                    <input type="text" name="" id="unit" v-model="currentProduct.unit" placeholder="" class="form-control" :class="classes">
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-md-6">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="origin_price"><span class="text-danger">*</span></label>
                                    <input type="number" name="" id="origin_price" v-model="currentProduct.origin_price" placeholder="" class="form-control" :class="classes">
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-md-6">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="price"><span class="text-danger">*</span></label>
                                    <input type="number" name="" id="price" v-model="currentProduct.price" placeholder="" class="form-control" :class="classes">
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-12">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="image"><span class="text-danger">*</span></label>
                                    <input type="text" name="" id="image" v-model="currentProduct.imageUrl" placeholder="" class="form-control" :class="classes">
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-md-6">
                                <label for="customFile"> 
                                    <i class="fas fa-spinner fa-spin" v-if="status.fileuploading"></i>
                                </label>
                                <input type="file" id="customFile" class="form-control" @change="uploadImage" ref="files">
                                <img class="product-img" alt="" :src="currentProduct.imageUrl">
                            </div>
                            <div class="col-md-6">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="description"><span class="text-danger">*</span></label>
                                    <textarea  name="" id="description" rows="9" v-model="currentProduct.description" placeholder="" class="form-control" :class="classes"></textarea>
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider> 
                            </div>
                            <div class="col-12">
                                <ValidationProvider rules="required" v-slot="{ errors, classes }">
                                    <label for="content"><span class="text-danger">*</span></label>
                                    <textarea  name="" id="content" rows="10" v-model="currentProduct.content" placeholder="" class="form-control" :class="classes"></textarea>
                                    <span class="invalid-feedback" >{{ errors[0] }}</span>
                                </ValidationProvider>
                            </div>
                            <div class="form-group">
                                <div class="form-check">
                                    <input class="form-check-input" type="checkbox" id="is_enabled" v-model="currentProduct.is_enabled" :true-value="1" :false-value="0">
                                    <label class="form-check-label" for="is_enabled">
                                    
                                    </label>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-third" data-bs-dismiss="modal"></button>
                        <button type="button" class="btn btn-abstract text-white" :disabled="invalid"  @click="updateProduct"></button>
                    </div>
                </div>
            </div>
        </div>
    </ValidationObserver>
</div>
</template>
<script>
import pagination from '../../backend/paginationManage';
import {mapGetters} from 'vuex'

export default {
    name:'ProductManage',
    data(){
        return{
            products:[],
            currentProduct:{},
            isNew:false,
            pagination:{},
            status:{
                fileuploading:false,
            },
            modaltitle:'',
            modalDelete:null,
            modalUpdate:null
        }
    },
    components:{
    pagination,
    },
    methods:{
        openModal(isNew ,item){
            if(isNew){
                this.currentProduct={};
                this.currentProduct.category='';
                this.isNew=true;
                this.modaltitle='';
            }else{
                this.currentProduct=Object.assign({},item);
                this.isNew=false;
                this.modaltitle='';
            }
            this.modalUpdate.show();
        },
        openModalDelete(item){
            this.currentProduct=Object.assign({},item);
            this.modalDelete.show();
        },
        getProducts(page =1){
            const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/products?page=${page}`;
            const vm =this;
            this.$store.dispatch('updatedLoading',true);
            this.$http.get(api).then((res)=>{
                if(res.data.success){
                    vm.products=res.data.products;
                    vm.pagination=res.data.pagination;     
                }else{     
                    vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
                }
                vm.$store.dispatch('updatedLoading',false);
            }).catch((err)=>{
                console.log(err);
            })  
        },
        deleteProduct(){
            const api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product/${this.currentProduct.id}`;
            const vm=this;
            this.$store.dispatch('updatedLoading',true); 
            this.$http.delete(api).then((res)=>{
                if(res.data.success){ 
                    vm.modalDelete.hide();
                    vm.getProducts();
                }else{
                    vm.modalDelete.hide();
                    vm.getProducts();
                    vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
                }
                vm.$store.dispatch('updatedLoading',false);
            }).catch((err)=>{
                console.log(err);
            });
        },
        uploadImage(){
            const uploadedfile =this.$refs.files.files[0];
            const vm =this;
            vm.status.fileuploading=true;
            const formdata =new FormData();
            formdata.append('file-to-upload',uploadedfile);
            const api =`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/upload`;
            this.$http.post(api,formdata,{headers:{'Content-Type':'multipart/form-data'}}).then((res)=>{
                if(res.data.success){
                    vm.$set(vm.currentProduct, 'imageUrl',res.data.imageUrl);
                }else{
                vm.$store.dispatch('updateMessage', {
                    message: res.data.message,
                    status: 'danger',
                });
                }
            vm.status.fileuploading=false;
            document.getElementById('customFile').value='';
            }).catch((err)=>{
                console.log(err);
            })   
        },
        updateProduct(){
            let api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product`;
            let HttpMethod='post';
            const vm =this;
            this.$store.dispatch('updatedLoading',true);
            if(!vm.isNew){
                api=`${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/product/${vm.currentProduct.id}`;
                HttpMethod='put';
            }
            this.$http[HttpMethod](api ,{data:vm.currentProduct}).then((res)=>{
                if(res.data.success){
                    vm.modalUpdate.hide();  
                    vm.getProducts();
                }else{
                    vm.modalUpdate.hide();  
                    vm.getProducts();
                    vm.$store.dispatch('updateMessage', {
                        message: res.data.message,
                        status: 'danger',
                    });
                }
                vm.$store.dispatch('updatedLoading',true);
            }).catch((err)=>{
                console.log(err);
            })
        }
    },
    computed:{
        ... mapGetters(['isLoading','checkoutVisible']),
    },
    created(){
        this.getProducts();
        this.$store.dispatch('updateCheckoutBtn',true);
    },
    mounted(){
        this.modalDelete=new bootstrap.Modal(document.getElementById('delProductModal'));
        this.modalUpdate=new bootstrap.Modal(document.getElementById('AddNewModal'));
    }
}
</script>